	module.exports = {"data":{"markdownRemark":{"html":"<p> <strong>WHEREAS</strong> </p>\n<ul>\n<li>\n<p>there is strong support for the principles, values and operational initiatives in developing services based on restorative justice approaches; </p>\n</li>\n<li>\n<p>arestorative justice approach should be the first priority in dealing with justice issues. Incarceration and social control mechanisms should be used only after restorative justice approaches are not successful, or not applicable; </p>\n</li>\n<li>\n<p>all justice personnel, including personnel in closed custody, should be selected, educated and committed to restorative justice principles and approaches. Continuing seminars and educational opportunities will need to be developed for staff to sustain this restorative approach; </p>\n</li>\n<li>\n<p>it will be necessary to develop systems and infrastructure that will form the basis of our service on restorative principles;</p>\n</li>\n</ul>\n<p><strong>BE IT RESOLVED</strong> the Liberal Party of Canada develop a comprehensive policy on Restorative Justice.(Saskatchewan)</p>","frontmatter":{"path":"/policy/restorative-justice","date":"Tue Mar 06 2018 00:08:44 GMT-0500 (EST)","title":"Restorative Justice"}}},"pathContext":{}}

